.alignleft {
  float: left;
  margin: 0.375em 1.75em 1.75em 0;
}

.alignright {
  float: right;
  margin: 0.375em 0 1.75em 1.75em;
}

.aligncenter {
  clear: both;
  display: block;
  margin: 0 auto 1.75em;
}

.wp-caption {
  margin-bottom: 1.75em;
  max-width: 100%;

  img[class*="wp-image-"] {
    display: block;
    margin: 0;
  }

  .wp-caption-text {
    color: $gray-600;
    font-style: italic;
  }
}

